import React from "react";

const TodoLi = ({ todoName, isActive, isImportant, todoObj,setTodo,todo }) => {
    const deleteBtn= () => {
setTodo(todo.filter((el) => el.id !== todoObj.id))
    }
    const doneBtn = () => {
        setTodo(todo.map(el => el.id === todoObj.id ?
            {...el,isActive: !el.isActive} : el  ))
    }
    const importantBtn = () => {
        setTodo(todo.map(el => el.id === todoObj.id ? {...el, isImportant: !isImportant} : el))
    }
  return (
    <div>
      <li className="todo-item">
        <p className = {`${isActive ? '' : 'done'} ${isImportant ? 'important' : ''} `}>{todoName}</p>
        <div>
          <button className={`${isActive ? '' : 'done'} btn`} onClick = {doneBtn}>done</button>
          <button className={` btn ${isImportant ? 'important' : ''} `} onClick = {importantBtn} >important</button>
          <button className = 'btn delete' onClick = {deleteBtn} >delete</button>
        </div>
      </li>
    </div>
  );
};

export default TodoLi;
