import React from "react";

const Nav = ({ navBtn, setNavBtn, todo, searchTodo, setSearchValue }) => {
  const navBtnHandler = (e) => {
    setNavBtn(e.target.value);
  };
  const inputHandler = (e) => {
    setSearchValue(e.target.value);
    // console.log(setSearchValue);
  };
  

  return (
    <div className="nav-panel">
      <div className="nav-box">
        <button
          className={`nav-btn  ${navBtn === "all" ? "active" : ""}`}
          onClick={navBtnHandler}
          value="all"
        >
          All
        </button>
        <button
          className={`nav-btn ${navBtn === "important" ? "active" : ""}`}
          onClick={navBtnHandler}
          value="important"
        >
          Imp
        </button>
        <button
          className={`nav-btn ${navBtn === "done" ? "active" : ""}`}
          onClick={navBtnHandler}
          value="done"
        >
          Done
        </button>
      </div>
      <div className="inp-holder">
        <input
          placeholder="Search"
          type="text"
          className="inp-search"
          onChange={inputHandler}
        />
      </div>
    </div>
  );
};

export default Nav;
