import React, { useEffect, useState } from "react";
import Header from "./header";
import Nav from "./nav";
import NewTodo from "./newTodo";
import "./style.css";
import Todo from "./todo";

const App = () => {
  const [todo, setTodo] = useState([]);
  const [todoName, setTodoName] = useState("");
  const [navBtn, setNavBtn] = useState('all')
  const [searchTodo, setSearchValue] = useState('')
  useEffect(() => {
    setTodo(( JSON.parse(localStorage.getItem('todo'))))
   
  }, [])
  useEffect(() => {
    localStorage.setItem('todo', JSON.stringify(todo))
  }, [todo])
 
  return (
    <div className="app">
      <div className="app-container">
        <div>
          <Header todo = {todo}/>
          <Nav navBtn = {navBtn} setNavBtn = {setNavBtn} todo = {todo} searchTodo = {searchTodo} setSearchValue = {setSearchValue} />
          <Todo todo={todo} setTodo = {setTodo} navBtn = {navBtn} searchTodo = {searchTodo}/>
        </div>

        <NewTodo
          setTodo={setTodo}
          todo={todo}
          setTodoName={setTodoName}
          todoName={todoName}
        />
      </div>
    </div>
  );
};

export default App;
