import React from "react";
import TodoLi from "./todoLi";

const Todo = ({ todo,setTodo,navBtn, searchTodo }) => {
  return (
    <ul className="todo-list">
      {todo.filter((el) => {
        switch(navBtn) {
          case 'all': {
            return el;
          }
          case 'important' : {
            return el.isImportant
          }
          case 'done' : {
            return !el.isActive
          }
        }
      }).filter((el) => el.todoName.includes(searchTodo))
      .map((el) => (
        <TodoLi
        setTodo = {setTodo}
          key={el.id}
          todoName={el.todoName}
          isActive={el.isActive}
          isImportant={el.isImportant}
          todoObj = {el}
          todo = {todo}
        />
      ))}
    </ul>
  );
};

export default Todo;
