import React from 'react';

const Header = ({todo}) => {
    const more = todo.filter(el => el.isActive).length
    const done = todo.filter(el => !el.isActive).length
    return (
        <div className = 'header'>
            <h1>to do list</h1>
            {todo.length === 0 ? '' : <p><span className = 'color-descr'>{more}</span>  more todo, <span className = 'color-descr'>{done} </span>done todo</p> }
        </div>
    );
};
export default Header;